<script>
  import niceScroll from 'jquery.nicescroll';
  import common from '../../../libs/common';
  import server from '../../../config/api';

  export default {
    computed: {
      showPeriodValue: function () {
        return this.taskPlan.periodType === 'D';
      },
      periodStartTimeOption: function () {
        switch (this.taskPlan.periodType) {
          case 'D':
            return this.periodStartDateOption;
          case 'W':
            return this.periodStartWeekOption;
          case 'H':
            return this.periodStartHalfMonthOption;
          case 'M':
            return this.periodStartMonthOption;
          case 'T':
            return this.periodStartTwoMonthOption;
          case 'S':
            return this.periodStartQuarterOption;
          case 'B':
            return this.periodStartHalfYearOption;
          case 'Y':
            return this.periodStartYearOption;
        }
        return null;
      },
      periodEndTimeOption: function () {
        switch (this.taskPlan.periodType) {
          case 'D':
            return this.periodEndDateOption;
          case 'W':
            return this.periodEndWeekOption;
          case 'H':
            return this.periodEndHalfMonthOption;
          case 'M':
            return this.periodEndMonthOption;
          case 'T':
            return this.periodEndTwoMonthOption;
          case 'S':
            return this.periodEndQuarterOption;
          case 'B':
            return this.periodEndHalfYearOption;
          case 'Y':
            return this.periodEndYearOption;
        }
        return null;
      }
    },
    data() {
      const validatorPeriodValue = (rule, value, callback) => {
        if(value === undefined || value === null) {
          callback(new Error());
        }
        callback();
      };
      const validatorPlanDateRange = (rule, value, callback) => {
        if(this.taskPlan.planStartDate === undefined || this.taskPlan.planStartDate === null
          || this.taskPlan.planStartDate.length === 0) {
          callback(new Error());
        }
        callback();
      };
      return {
        isSaved: false,
        common: common,
        canReturn: this.getYunSessionLength('route_list') > 1,
        currentOrgId: localStorage.current_org_id,
        taskPlan: {
          title: undefined,
          summary: undefined,
          pid: localStorage.default_pid,
          creatorOrgId: localStorage.current_org_id,
          creatorUid: localStorage.access_user_id,
          taskType: 'PATROL', //  
          periodType: undefined, // N()|D()|W()|H()|M()|S()|Y()
          periodValue: undefined, // 
          periodStartTime: undefined, // 
          periodEndTime: undefined, // 
          planStartDate: undefined, // 
          planEndDate: undefined, // 
          checkInQrInd: 'N', // 
          targetOrgId: localStorage.current_org_id, // id
          targetBuildingId: undefined,
          primaryExecutorUid: undefined, // 
          secondaryExecutorUid: undefined, // 
          content: ''
        },
        taskPlanRuleValidate: {
          title: [{
            required: true, trigger: 'blur'
          }],taskType: [{
            required: true, trigger: 'blur'
          }],planDateRange: [{
            required: true, validator: validatorPlanDateRange, trigger: 'blur'
          }],periodType: [{
            required: true, trigger: 'blur'
          }],periodStartTime: [{
            required: true, trigger: 'blur'
          }],periodEndTime: [{
            required: true, trigger: 'blur'
          }],periodValue: [{
            required: true, validator: validatorPeriodValue, trigger: 'blur'
          }],targetOrgId: [{
            required: true, trigger: 'blur'
          }],targetBuildingId: [{
            required: true, trigger: 'blur'
          }]
        },
        taskContent: [],
        periodValueOption: [
          {code: 1, value: '1'},
          {code: 2, value: '2'},
          {code: 3, value: '3'},
          {code: 4, value: '4'},
          {code: 5, value: '5'},
          {code: 6, value: '6'},
          {code: 7, value: '7'},
          {code: 8, value: '8'},
          {code: 9, value: '9'},
          {code: 10, value: '10'},
          {code: 11, value: '11'},
          {code: 12, value: '12'},
          {code: 13, value: '13'},
          {code: 14, value: '14'},
          {code: 15, value: '15'},
          {code: 16, value: '16'},
          {code: 17, value: '17'},
          {code: 18, value: '18'},
          {code: 19, value: '19'},
          {code: 20, value: '20'},
          {code: 21, value: '21'},
          {code: 22, value: '22'},
          {code: 23, value: '23'}
        ],
        periodStartDateOption: [
          {code: '0', value: '00:00'},
          {code: '1', value: '01:00'},
          {code: '2', value: '02:00'},
          {code: '3', value: '03:00'},
          {code: '4', value: '04:00'},
          {code: '5', value: '05:00'},
          {code: '6', value: '06:00'},
          {code: '7', value: '07:00'},
          {code: '8', value: '08:00'},
          {code: '9', value: '09:00'},
