          {code: '11', value: '11:00'},
          {code: '12', value: '12:00'},
          {code: '13', value: '13:00'},
          {code: '14', value: '14:00'},
          {code: '15', value: '15:00'},
          {code: '16', value: '16:00'},
          {code: '17', value: '17:00'},
          {code: '18', value: '18:00'},
          {code: '19', value: '19:00'},
          {code: '20', value: '20:00'},
          {code: '21', value: '21:00'},
          {code: '22', value: '22:00'},
          {code: '23', value: '23:00'}
        ],
        periodEndDateOption: [
          {code: '0', value: '00:00'},
          {code: '1', value: '01:00'},
          {code: '2', value: '02:00'},
          {code: '3', value: '03:00'},
          {code: '4', value: '04:00'},
          {code: '5', value: '05:00'},
