    methods: {
      returnPage: function() {
        // sessionStorage
        this.parentRoute = this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      initData: function () {
        let page = this.topYunSession('route_list');
        let planId = page.planId;
        if(planId === undefined) {
          // 
          this.searchBuildingList();
          return;
        }
        // 
        this.taskTypeDisabled = true;
        this.$http.get(server.api.taskPlan.getPlan.replace('{planId}', planId)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          // 
          if(response.body.data.planStartDate !== undefined && response.body.data.planStartDate !== null) {
            response.body.data.planStartDate = new Date(response.body.data.planStartDate);
          }
          if(response.body.data.planEndDate !== undefined && response.body.data.planEndDate !== null) {
            response.body.data.planEndDate = new Date(response.body.data.planEndDate);
          }
          this.taskPlan = response.body.data;
          this.searchBuildingList();
          this.taskContent = (JSON.parse(response.body.data.content)).taskItems;
          // option
          let periodStartTime = this.taskPlan.periodStartTime;
          let periodEndTime = this.taskPlan.periodEndTime;
          this.taskPlan.periodEndTime = this.taskPlan.periodStartTime = undefined;
          this.$nextTick(() => {
            this.taskPlan.periodEndTime = periodEndTime;
            this.taskPlan.periodStartTime = periodStartTime;
          });
        }, (error) => {
          this.$Message.error("");
        });
      },
      fetchData: function () {
        this.$nextTick(() => {
          $(".ivu-select-dropdown").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "4px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
          $(".auto-height-table .ivu-table-body").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
          $("#device_type_tree").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            railpadding: { top: 0, right: 2, left: 0, bottom: 0 }, // 
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      },
      S4: function() {
        return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
      },
      guid: function() {
        return (this.S4()+this.S4()+"-"+this.S4()+"-"+this.S4()+"-"+this.S4()+"-"+this.S4()+this.S4()+this.S4());
      },
      searchBuildingList: function() {
        this.$http.get(server.api.organization.buildingList.list.replace('{orgId}', this.currentOrgId)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          this.buildingList = response.body.data;
          if((this.taskPlan.targetBuildingId === undefined || this.taskPlan.targetBuildingId === null)
            && this.buildingList !== null && this.buildingList.length > 0) {
            this.taskPlan.targetBuildingId = this.buildingList[0].buildingId;
          }
        }, (error) => {
          this.$Message.error("");
        });
      },
      targetBuildingChange: function(val) {
        if(val === undefined || val === null || val.length === 0) {
          return;
        }
        if(!this.isInitData) {
          // 
          this.taskPlan.primaryExecutorUid = undefined;
          // 
          let index = this.taskContent.length;
          while(index --) {
            if(this.taskContent[index].deviceTypeId !== undefined && this.taskContent[index].deviceTypeId !== null) {
              this.taskContent.splice(index, 1);
            }
          }
        }
        this.$http.get(server.api.organization.building.getLiablePerso.replace('{buildingId}', val)
          + '?relationType=PARTICIPATE').then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          this.userList = response.body.data;
          if((this.taskPlan.primaryExecutorUid === null || this.taskPlan.primaryExecutorUid === undefined || this.taskPlan.primaryExecutorUid.length === 0)
            && this.userList !== null && this.userList.length > 0) {
            this.taskPlan.primaryExecutorUid = this.userList[0].uid;
          }
          if(this.isInitData) {
            this.originalTaskPlan = JSON.parse(JSON.stringify(this.taskPlan));
            let taskPlanContent = {
              type: this.taskPlan.taskType,
              taskItems: this.taskContent
            };
            this.originalTaskPlan.content = JSON.stringify(taskPlanContent);
            this.isInitData = false;
          }
        }, (error) => {
          this.$Message.error("");
        });
      },

      searchMaint: function(deviceType) {
        // 
        this.$http.get(server.api.taskPlan.getMaintTree + '?deviceType=' + deviceType).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          let knowledgeObj = JSON.parse(JSON.stringify(this.knowledgeTree[0].children));
          // 
          let itemContent = _.find(knowledgeObj, {type: 'PATROL'});
          itemContent.children = response.body.data.patrolKbs;
          for(let index in itemContent.children) {
            itemContent.children[index].title = itemContent.children[index].summary;
            // 
            if(_.findIndex(this.selectedItemContentList, {id: itemContent.children[index].id}) !== -1) {
              itemContent.children[index].checked = true;
            }
          }
          // 
          itemContent = _.find(knowledgeObj, {type: 'UPKEEP'});
          itemContent.children = response.body.data.upkeepKbs;
          for(let index in itemContent.children) {
            itemContent.children[index].title = itemContent.children[index].summary;
            // 
            if(_.findIndex(this.selectedItemContentList, {id: itemContent.children[index].id}) !== -1) {
              itemContent.children[index].checked = true;
            }
          }
          // 
          itemContent = _.find(knowledgeObj, {type: 'DETECT'});
          itemContent.children = response.body.data.detectKbs;
          for(let index in itemContent.children) {
            itemContent.children[index].title = itemContent.children[index].summary;
            // 
            if(_.findIndex(this.selectedItemContentList, {id: itemContent.children[index].id}) !== -1) {
              itemContent.children[index].checked = true;
            }
          }
          this.$set(this.knowledgeTree[0], 'children', knowledgeObj);
        }, (error) => {
          this.$Message.error("");
        });
      },

      changePlanDate: function(val) {
        this.taskPlan.planStartDate = val[0];
        this.taskPlan.planEndDate = val[1];
      },

      removeTaskContent: function(itemId) {
        let taskContentIndex = _.findIndex(this.taskContent, {itemId: itemId});
        if(taskContentIndex !== -1) {
          this.taskContent.splice(taskContentIndex, 1);
        }
        this.$nextTick(() => {
          $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
        });
      },
      /**  **/
      setOptionAbled: function (optionList) {
        for (let index in optionList) {
          optionList[index].disabled = false;
        }
      },
      /**
       * option
       * @param optionList option
       * @param value 
       * @param isStartTime 
       */
      setOptionDisabled: function (optionList, value, isStartTime) {
        for (let index in optionList) {
          if (isStartTime ? this.determineSize(optionList[index].code, value)
            : this.determineSize(value, optionList[index].code)) {
            optionList[index].disabled = true;
          } else {
            optionList[index].disabled = false;
          }
        }
      },
      determineSize: function (valueA, valueB) {
        // periodTypeA>Btrue
        if (this.taskPlan.periodType === 'M') {
          // L
          if(valueA === 'L' && valueB === 'L') {
            return false;
          }
          if (valueA === 'L') {
            return true;
          }
          if (valueB === 'L') {
            return false;
          }
        }
        return parseInt(valueA) > parseInt(valueB);
      },
      changePeriodType: function () {
        this.taskPlan.periodStartTime = undefined;
        this.taskPlan.periodEndTime = undefined;
        switch(this.taskPlan.periodType) {
          case 'D':
            this.setOptionAbled(this.periodStartDateOption);
            this.setOptionAbled(this.periodEndDateOption);
            break;
          case 'W':
            this.setOptionAbled(this.periodStartWeekOption);
            this.setOptionAbled(this.periodEndWeekOption);
            break;
          case 'H':
            this.setOptionAbled(this.periodStartHalfMonthOption);
            this.setOptionAbled(this.periodEndHalfMonthOption);
            break;
          case 'M':
            this.setOptionAbled(this.periodStartMonthOption);
            this.setOptionAbled(this.periodEndMonthOption);
            break;
          case 'T':
            this.setOptionAbled(this.periodStartTwoMonthOption);
            this.setOptionAbled(this.periodEndTwoMonthOption);
            break;
          case 'S':
            this.setOptionAbled(this.periodStartQuarterOption);
            this.setOptionAbled(this.periodEndQuarterOption);
            break;
          case 'B':
            this.setOptionAbled(this.periodStartHalfYearOption);
            this.setOptionAbled(this.periodEndHalfYearOption);
            break;
          case 'Y':
            this.setOptionAbled(this.periodStartYearOption);
            this.setOptionAbled(this.periodEndYearOption);
            break;
        }
        this.$nextTick(() => {
          $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          $(".ivu-select-dropdown").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "4px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      },
      changePeriodStartTime: function (value) {
        // 
        if (this.taskPlan.periodEndTime !== undefined && this.determineSize(value, this.taskPlan.periodEndTime)) {
          this.taskPlan.periodEndTime = undefined;
        }
        // disabled
        switch(this.taskPlan.periodType) {
          case 'D':
            this.setOptionDisabled(this.periodEndDateOption, value, false);
            break;
          case 'W':
            this.setOptionDisabled(this.periodEndWeekOption, value, false);
            break;
          case 'H':
            this.setOptionDisabled(this.periodEndHalfMonthOption, value, false);
            break;
          case 'M':
            this.setOptionDisabled(this.periodEndMonthOption, value, false);
            break;
          case 'T':
            this.setOptionDisabled(this.periodEndTwoMonthOption, value, false);
            break;
          case 'S':
            this.setOptionDisabled(this.periodEndQuarterOption, value, false);
            break;
          case 'B':
            this.setOptionDisabled(this.periodEndHalfYearOption, value, false);
            break;
          case 'Y':
            this.setOptionDisabled(this.periodEndYearOption, value, false);
            break;
        }
      },
      changePeriodEndTime: function (value) {
        // 
        if (this.taskPlan.periodStartTime !== undefined && this.determineSize(this.taskPlan.periodStartTime, value)) {
          this.taskPlan.periodStartTime = undefined;
        }
        // disabled
        switch(this.taskPlan.periodType) {
          case 'D':
            this.setOptionDisabled(this.periodStartDateOption, value, true);
            break;
          case 'W':
            this.setOptionDisabled(this.periodStartWeekOption, value, true);
            break;
          case 'H':
            this.setOptionDisabled(this.periodStartHalfMonthOption, value, true);
            break;
          case 'M':
            this.setOptionDisabled(this.periodStartMonthOption, value, true);
            break;
          case 'T':
            this.setOptionDisabled(this.periodStartTwoMonthOption, value, true);
            break;
          case 'S':
            this.setOptionDisabled(this.periodStartQuarterOption, value, true);
            break;
          case 'B':
            this.setOptionDisabled(this.periodStartHalfYearOption, value, true);
            break;
          case 'Y':
            this.setOptionDisabled(this.periodStartYearOption, value, true);
            break;
        }
      },
