      // 
      chooseDevice: function() {
        // 
        this.searchFloorId = undefined;
        this.searchDeviceTypeId = undefined;
        this.searchDeviceType = undefined;
        this.searchDeviceName = undefined;
        // 
        this.deviceDatas = [];
        if(this.taskPlan.targetBuildingId === undefined || this.taskPlan.targetBuildingId === null
          || this.taskPlan.targetBuildingId.length === 0) {
          this.$Message.error("");
          return;
        }
        // 
        this.searchDeviceTypeList();
        // 
        this.searchBuildingFloorList();
        // datas
        this.showChooseDeviceModal = true;
        this.searchDeviceList(1);
      },
      searchDeviceList: function(pageNum) {
        let url = server.api.organization.device.getDevices + '?level=BUILDING&buildingId='
          + this.taskPlan.targetBuildingId + '&pageSize=' + this.chooseDevicePageSize + '&pageNum=' + pageNum;
        if(this.searchFloorId !== undefined) {
          url += '&floorId=' + this.searchFloorId;
        }
        if(this.searchDeviceType !== undefined) {
          if(this.searchDeviceType === 'TYPE') {
            url += '&deviceType=' + this.searchDeviceTypeId;
          } else if(this.searchDeviceType === 'SYSTEM') {
            url += '&systemType=' + this.searchDeviceTypeId;
          }
        }
//        if(this.searchDeviceTypeId !== undefined) {
//          url += '&deviceType=' + this.searchDeviceTypeId;
//        }
        if(this.searchDeviceName !== undefined) {
          url += '&searchParam=' + this.searchDeviceName;
        }
        this.$http.get(url).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          this.chooseDevicePageNum = pageNum;
          this.chooseDevicePageTotal = response.body.data.total;
          this.chooseDeviceList = response.body.data.rows;
          for(let index in this.chooseDeviceList) {
            let device = this.chooseDeviceList[index];
            // 
            if(_.findIndex((_.differenceBy(this.taskContent, [{'deviceTypeId': undefined || null}], 'deviceTypeId')),
              {itemId: device.deviceId}) !== -1) {
              device._checked = true;
              device._disabled = true;
            }
            // 
            if(_.findIndex((_.differenceBy(this.deviceDatas, [{'deviceTypeId': undefined || null}], 'deviceTypeId')),
              {deviceId: device.deviceId}) !== -1) {
              device._checked = true;
            }
          }
        }, (error) => {
          this.$Message.error("");
        });
        this.$nextTick(() => {
          let chooseDeviceTable = $(".auto-height-table .ivu-table-body");
          if(chooseDeviceTable.getNiceScroll() === undefined || chooseDeviceTable.getNiceScroll() === null
            || chooseDeviceTable.getNiceScroll().length === 0) {
            chooseDeviceTable.niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "7px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          } else {
            chooseDeviceTable.getNiceScroll().resize();
          }
        });
      },
      searchBuildingFloorList: function() {
        this.$http.get(server.api.organization.building.getFloors.replace('{buildingId}',this.taskPlan.targetBuildingId)).then((response) => {
          this.floorList = response.body.data;
