            {id: checkedNodes[index].id}) !== -1) {
            continue;
          }
          let content = {};
          content.id = checkedNodes[index].id;
          content.type = checkedNodes[index].type;
          content.summary = checkedNodes[index].summary;
          content.detail = checkedNodes[index].detail;
          if (this.selectedItemContentList === undefined || this.selectedItemContentList === null) {
            this.selectedItemContentList = [];
          }
          this.selectedItemContentList.push(content);
        }
        // 
        for (let index=0; index<this.selectedItemContentList.length; index++) {
          if (this.selectedItemContentList[index].type === null) {
