          taskItems: this.taskContent
        };
        this.taskPlan.content = JSON.stringify(taskPlanContent);
        if(this.taskPlan.planEndDate === undefined || this.taskPlan.planStartDate === null
          || this.taskPlan.planStartDate.length === 0) {
          this.taskPlan.planStartDate = null;
        }
        if(this.taskPlan.planId === undefined) {
          // 
          this.$http.post(server.api.taskPlan.addPlan.replace('{orgId}', this.taskPlan.targetOrgId)
            .replace('{buildingId}', this.taskPlan.targetBuildingId), this.taskPlan).then((response) => {
            if(!response.body.meta.success) {
              this.$Message.error("");
              return;
            }
            this.$Message.success("");
            // 
            this.isSaved = true;
            this.returnPage();
//            this.taskPlan.planId = response.body.data;
//            this.originalTaskPlan = JSON.parse(JSON.stringify(this.taskPlan));
//            this.taskTypeDisabled = true;
          }, (error) => {
            this.$Message.error("");
          });
        } else {
          // 
          this.editTaskPlan();
        }
      },
      editTaskPlan: function() {
        if(this.isAfterNowPeriod(this.taskPlan.periodType, this.taskPlan.planStartDate)) {
          // 
//          this.updateTaskPlan('');
          this.updateTaskPlan('');
        } else {
          // 
          this.$http.get(server.api.taskPlan.getNowTasks.replace('{planId}', this.taskPlan.planId)).then((response) => {
            if(!response.body.meta.success) {
              this.$Message.error("");
              return;
            }
            let taskList = response.body.data;
            if(taskList === null || taskList === undefined || taskList.length === 0) {
              // 
//              this.updateTaskPlan('');
              this.updateTaskPlan('');
              return;
            } else {
              this.unCompletedTaskIdList = [];
              for(let index in taskList) {
                // 
                if(taskList[index].status !== 'DONE') {
                  this.unCompletedTaskIdList.push(taskList[index].taskId);
                }
              }
              if(this.unCompletedTaskIdList.length === 0) {
                // 
//                this.updateTaskPlan('');
                this.updateTaskPlan('');
                return;
              } else {
                // 
                this.confirmUpdate = true;
              }
            }
          }, (error) => {
            this.$Message.error("");
          });
        }
      },
      updateTaskPlanWithoutDeleteTasks: function() {
//        this.updateTaskPlan('');
        this.updateTaskPlan('');
        this.confirmUpdate = false;
      },
      updateTaskPlanAfterDeleteTasks: function() {
        let idList = '';
        for(let index in this.unCompletedTaskIdList) {
          let id = JSON.stringify(this.unCompletedTaskIdList[index]);
          id = id.substr(1, id.length-2);
          idList += id;
          if(parseInt(index) !== this.unCompletedTaskIdList.length -1) {
            idList += ',';
          }
        }
        this.$http.delete(server.api.taskPlan.deleteTask.replace('{taskId}', idList)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error(response.body.meta.message);
            return;
          }
//          this.updateTaskPlan('');
          this.updateTaskPlan('');
        }, (error) => {
          this.$Message.error("");
        });
        this.confirmUpdate = false;
      },
      updateTaskPlan: function(successMessage) {
        this.$http.put(server.api.taskPlan.getPlan.replace('{planId}', this.taskPlan.planId), this.taskPlan).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
