          this.returnPage();
//          this.originalTaskPlan = JSON.parse(JSON.stringify(this.taskPlan));
        }, (error) => {
          this.$Message.error("");
        });
      },
      isAfterNowPeriod: function(periodType, planStartDate) {
        // 23:59:59
        let today = new Date();
        // let date = new Date(2018,2,8,23,59,59);  2018-03-08 23:59:59 
        let date = new Date(today.getFullYear(), today.getMonth(), today.getDate(), 23, 59, 59);
        switch(periodType) {
          case 'D':
            // 235959
            break;
          case 'W':
            // 
            date.setDate(date.getDate() + (6 - date.getDay()));
            break;
          case 'H':
            // 15
            if(date.getDate() > 15) {
              // 
              date.setDate(1);
              date.setMonth(date.getMonth() +1);
              date.setDate(date.getDate() - 1);
              break;
            } else {
              // 15
              date.setDate(15);
              break;
            }
          case 'M':
            // 
            date.setDate(1);
            date.setMonth(date.getMonth() +1);
            date.setDate(date.getDate() - 1);
            break;
          case 'T':
            // 
            date.setDate(1);
            date.setMonth((date.getMonth() +1) % 2 ? date.getMonth() + 2 : date.getMonth() + 1);
            date.setDate(date.getDate() - 1);
            break;
          case 'S':
            // 
            date.setDate(1);
            date.setMonth((date.getMonth() +1) % 3 ? (date.getMonth() + (4 - (date.getMonth() +1) % 3)) : date.getMonth() + 1);
            date.setDate(date.getDate() - 1);
            break;
          case 'B':
            // 
            date.setDate(1);
            date.setMonth((date.getMonth() +1) % 6 ? (date.getMonth() + (7 - (date.getMonth() +1) % 6)) : date.getMonth() + 1);
            date.setDate(date.getDate() - 1);
            break;
          case 'Y':
            // 12
            date.setDate(1);
            date.setMonth(12);
            date.setDate(date.getDate() - 1);
            break;
        }
        return new Date(planStartDate).getTime() > date.valueOf();
      },
      resetTaskPlan: function() {
        // 
        let taskType = undefined, buildingId = undefined;
        if(this.taskPlan.planId !== undefined && this.taskPlan.planId !== null
          && this.taskPlan.planId.length !== 0) {
          taskType = this.taskPlan.taskType;
          buildingId = this.taskPlan.targetBuildingId;
        }
        this.taskPlan ={
          title: undefined,
          summary: undefined,
          pid: localStorage.default_pid,
          creatorOrgId: localStorage.current_org_id,
          creatorUid: localStorage.access_user_id,
          taskType: taskType, //  
          periodType: undefined, // N()|D()|W()|H()|M()|S()|Y()
          periodValue: undefined, // 
          periodStartTime: undefined, // 
          periodEndTime: undefined, // 
          planStartDate: undefined, // 
          planEndDate: undefined, // 
          checkInQrInd: 'N', // 
          targetOrgId: localStorage.current_org_id, // id
          targetBuildingId: buildingId,
          primaryExecutorUid: undefined, // 
          secondaryExecutorUid: undefined, // 
          content: ''
        };
        this.taskContent = [];
      },

    },
    mounted() {
      this.isInitData = true;
      this.initData();
      this.fetchData();
      window.self = this;
    },
    beforeRouteLeave(to, from, next) {
      console.info('MonitorView Before Route Leave');
      let taskPlanContent = {
        type: this.taskPlan.taskType,
        taskItems: this.taskContent
      };
      let taskPlan = JSON.parse(JSON.stringify(this.taskPlan));
      taskPlan.content = JSON.stringify(taskPlanContent);
      if(!this.isSaved && !_.isEqual(this.originalTaskPlan, taskPlan)) {
        this.$Modal.confirm({
          title: '',
          content: '',
          onOk: () => {
            next();
          },
          onCancel: () => {
            if(this.getYunSessionLength('route_list') === 1) {
              this.pushYunSession('route_list', this.parentRoute);
            }
          }
        });
      } else {
        next();
      }
    }

  }
</script>



